*{
  padding:0;
  margin:0;
  box-sizing:border-box;
}

.contain{
  width:100vw;
  height:100vh;
  display:grid;
  place-items:center;
  position:relative;
}

.containCard{
  width:70%;
  height: 70%;
  overflow: hidden;
  position: absolute;
  display:flex;
  flex-direction:row;
  border-radius: 2vmin;
  box-shadow: 0px 0px 25px black;
}

article{
  width: 100%;
  height: 100%;
  position:relative;
  text-align: center;
  
  position: absolute;
  opacity: 0;
  transition: all 400ms linear;
 
 
}

article >img{
  width:17vmin;
  height:15vmin;
  border-radius:50%;
  position: absolute;
  top:15%;
  left:45%;
  display: flex;
  place-items: center;
  justify-content: center;
}
.name{
  font-size:3vmin;
  color:brown;
  font-weight: bold;
  text-transform:uppercase;
  position: absolute;
  top:40%;
   width:100%;
   text-align: center;
}
.title{
  font-size:2vmin;
  text-align: center;
  position: absolute;
  width: 100%;
  top:45%;
}
.quote{
  width:80%;
  text-align: center;
  position: absolute;
  bottom:25%;
  left:15%;
}
.right{
  position: absolute;
  top:50%;
  right: 0%;
  font-size:4vmin;
}
.left{
  left:0%;
  position:absolute;
  top:50%;
  font-size:4vmin;
}

article.activeSlide{
  transform: translateX(0%);
  opacity: 1;
}
article.lastSlide{
  transform:translateX(-100%);
  
}
article.nextSlide{
  transform:translateX(100%)
}

@media only screen and (max-width:550px){
  .containCard{
    width: 90%;
    height:90%;;
  }
  article > img{
    width:45vmin;
    height:45vmin;
    top:5%;
    left:25%;
  }
  .name{
    font-size: 4vmin;
  }
  .title{
    font-size: 3vmin;
  }
  .quote{
    left:10%;
  }
}